<template>
    <div class="container-app">
      <div class="container-quiz">
        <div class="header-quiz" :class="{'blur': answered}">
          <h1>Ai thông minh hơn Cien?</h1>
        </div>
        <div class="step-progress" :style="cssVars"></div>
        <div class="box">

          <div class="box-question" :class="{'blur': answered}">
            <h2>Câu hỏi số {{i+1}}</h2>
            <p>{{this.quiz[i].question}}</p>
          </div>
          <h1 :class="{'fade-in' : answered, 'hide' : !answered}">{{msg}}</h1>
          <div class="box-propositions" :class="{'blur': answered}">
            <ul>
              <li
                  v-for="(answer, index) in this.quiz[i].answers"
                  :key="index"
                  @click="checkAnswer(answer)"
                  :class="{'correct-answer': answer.correct && answered, 'wrong-answer': answered}">

                {{answer.content}}
              </li>
            </ul>
          </div>
        </div>
        <div class="footer-quiz" :class="{'blur': answered}">
          <div class="box-button">
            <button  @click="skipQuiz" :class="{'hide' : answered}">Bỏ qua</button>
            <button @click="nextQuiz" :class="{'hide' : finished}">Tiếp</button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data(){
    return{
      quiz: [
        {
          question: "Ngọc là người chạy nhanh thứ 50 và cũng là người chạy chậm thứ 50 trong cuộc thi ở trường. Giả sử không có hai người nào chạy cùng tốc độ, tổng cộng có bao nhiêu học sinh ở trường Matt tham gia chạy?",
          answers: [
            {content: '50'},
            {content: '51'},
            {content: '99', correct: true},
            {content: '100'}
          ]
        },
        {
          question: "Anh Quyết đầu tư bất động sản, và cứ mỗi tháng số tiền ấy lại sinh lãi gấp đôi. Sau một năm – 12 tháng, số tiền đã lên tới 100 triệu đôla. Vậy vào lúc nào thì anh Quyết có số tiền là 50 triệu?",
          answers: [
            {content: 'tháng thứ 2'},
            {content: 'tháng thứ 11', correct: true},
            {content: 'tháng thứ 10'},
            {content: 'tháng thứ 6'},
          ]
        },
        {
          question: "Nếu x và y là các số nguyên tố thì các giá trị nào trong các giá trị sau không thể là tổng của x và y?",
          answers: [
            {content: '9'},
            {content: '13'},
            {content: '16'},
            {content: '23', correct: true},
          ]
        },
        {
          question: "Một cửa hàng buôn đồ có 9 đồng tiền vàng giống hệt nhau, tuy nhiên có 1 đồng trong đó là giả và nhẹ hơn đồng tiền thật. Số lần cân ít nhất để tìm ra đồng tiền giả là bao nhiêu?",
          answers: [
            {content: '1'},
            {content: '2', correct: true},
            {content: '3'},
            {content: '4'},
          ]
        },
        {
          question: "Cho 0,2 mol hỗn hợp gồm CO2 và hơi nước qua than nung đỏ thu được 0,35 mol hỗn hợp khí X gồm CO,CO2,H2.Dẫn toàn bộ X qua dung dịch chứa x mol NaHCO3 và y mol Na2CO3,thu được dung dịch Y chứa 27,4 gam chất tan, khí thoát ra còn CO và H2.Cô cạn dung dịch Y,nung đến khối lượng không đổi thu được 21,2 gam chất rắn.Giá trị của y là:",
          answers: [
            {content: '0,15', correct: true},
            {content: '0,35'},
            {content: '0,25'},
            {content: '0,10'},
          ]
        },
      ],
      answered: false,
      point: 0,
      msg: '',
      i : 0,
    }
  },
  name: 'App',
  components: {
    //HelloWorld
  },
  methods:{
    checkAnswer(answer) {
      this.answered = true
      if (answer.correct) {
        this.point++
        this.msg = "Chuẩn vãi!"
      } else {
        this.msg = 'sai òi!'
      }
      if (this.i === this.quiz.length - 1) {
        this.endQuiz()
      }
      setTimeout(() => this.msg = '', 1000);
    },
    nextQuiz() {
      if (!this.answered) {
        this.$alert("Chọn 1 đáp án hoặc click bỏ qua đê bạn ei!");
        return
      }
      this.i++
      this.answered = false
      this.msg = ''
    },
    skipQuiz(){
      if (this.i < this.quiz.length - 1) {
        this.i++
      } else {
        this.$confirm("Điểm của pạn là " + this.point + ". Muốn chơi lại hong?").then(() => {
          window.location.reload();
        });
      }
    },
    endQuiz() {
      setTimeout(() => this.$confirm("Điểm của pạn là " + this.point + ". Muốn chơi lại hong?").then(() => {
        window.location.reload();
      }), 1000);

    }
  },
  computed: {
    cssVars() {
      return {
        'width': 100*(this.i + 1)/(this.quiz.length) + '%',
      }
    },
    finished() {
      return this.i === this.quiz.length - 1
    }
  }
}
</script>
<style>
.correct-answer {
  background-color: green !important;
}
.wrong-answer {
  background-color: red;
}


.fade-in {
  padding-top: 20px;
  font-size: 200px;
  opacity: 0;
  color: black;
  z-index:100;
  position: absolute;
  left: 15%;
  right: 15%;
  animation: fade 1s linear;

}
@keyframes fade {
  0%,100% { opacity: 0}
  50% { transform: scale(0.7); opacity: 1; display: none }
}

.blur {
  filter: blur(0);
  animation: blur 1s linear;
}
@keyframes blur {
  0%,100% { filter: blur(0) }
  50% { filter: blur(4px) }
}
.hide {
  display: none;
}
</style>
